<template>
  <div v-show="!style || !style.hidden"
       class="table-container"
       :class="style.class"
       :style="style.position"
       :id="id + '-container'">
    <slot name="table-before"></slot>
    <!--  -->
    <div :id="id" class="table-ins">
    </div>
    <el-table :id="id"
              :ref="id"
              class="table-ins"
              :data="data"
              :max-height="option.maxHeight"
              :highlight-current-row="option.highlightCurrentRow || false"
              :stripe="option.stripe || false"
              :border="option.border || false"
              :default-sort="option.defaultSort"
              @cell-click="onCellClick"
              @row-click="onRowClick"
              @header-click="onHeaderClick">
      <template v-for="(column, index) in option.columns">
        <template v-if="column.type =='index'">
          <el-table-column v-if="!column.hidden"
                           :key="index"
                           v-show=""
                           type="index"
                           :label="column.text"
                           :width="column.width"
                           :fixed="column.fixed || false">
          </el-table-column>
        </template>
        <template v-else-if="!column.type || column.type =='text'">
          <el-table-column v-if="!column.hidden"
                           :key="index"
                           :prop="column.prop"
                           :width="column.width"
                           :sortable="column.sortable"
                           :fixed="column.fixed || false"
                           :label="column.text"
                           :formatter="column.formatter">
          </el-table-column>
        </template>
        <template v-else-if="column.type == 'component'">
          <el-table-column v-if="!column.hidden"
                           :key="index"
                           :width="column.width"
                           :fixed="column.fixed || false"
                           :label="column.text">
            <template slot-scope="scope">
              <component :is="components[index].loader.default"
                         :config="components[index].configs[scope.$index]"></component>
            </template>
          </el-table-column>
        </template>
      </template>
    </el-table>
    <slot name="table-after"></slot>
  </div>
</template>

<script>
  import Config from "../../../../config/config"
  import _ from 'lodash'

  export default {
    name: "Table",
    props: ['config'],
    data() {
      return {
        id: null,
        table: null,
        data: this.config.data,
        style: this.config.style || {class: null, position: null},
        binder: this.config.binder,
        option: this.config.option,
        handler: this.config.handler,
        event: this.config.event,
        proxy: this.config.proxy,
        components: []
      }
    },
    created() {
      if (!this.config) {
        throw new Error("Tableconfig")
      }
      // id
      this.id = this.config.id || "table-" + new Date().getTime();
      this.initPosition()
      this.initBinder()
    },
    mounted() {
      this.table = this.$refs[this.id]
      this.init()
    },
    beforeDestroy() {
      // 
      this.$store.commit(Config.MUTATIONS.SCREEN.UNREGISTER, {
        comp: this
      })
    },
    computed: {},
    methods: {
      /**
       * 
       */
      render() {
        this.transformData();
        this.dynamicLoadComponent(this.option.columns)
        this.table.doLayout()
      },
      /**
       * 
       */
      initPosition() {
        if (this.style.position) {
          let zIndex = this.style.position.zIndex || 0
          this.style.position = _.mapValues(this.style.position, function (o) {
            return o + "px"
          })
          _.set(this.style, 'position.zIndex', zIndex)
          _.set(this.style, 'position.position', 'absolute')
        }
      },
      /**
       * 
       */
      initBinder() {
        for (let key in this.binder) {
          let prop = this.binder[key][0]
          let storeProp = this.binder[key][1]
          this.$store.commit(Config.MUTATIONS.SCREEN.REGISTER, {
            store: storeProp,
            prop: prop,
            comp: this
          })
        }
      },
      /**
       * 
       */
      init() {
        this.load()
      },
      /**
       * 
       */
      load() {
        if (this.proxy.remote !== false) {
          this.loadRemoteData();
        } else {
          this.render()
          this.proxyMethod(this.event, "onLoad", [this])
        }
      },
      /**
       * 
       */
      loadRemoteData() {
        if (!this.proxy || !this.proxy.remote) {
          return;
        }
        if (!this.proxy.url) {
          throw new Error("TableURL")
        }
        if (!this.proxy.method) {
          this.proxy.method = "POST"
        }
        this.ajax({
          method: this.proxy.method,
          url: this.proxy.url,
          data: this.proxy.param,
        }).then(response => {
          this.proxy.data = response.data
          this.render()
          this.proxyMethod(this.event, "onLoad", [this])
        }).catch(error => {
          this.$message.error(error.message)
        });
      },
      /**
       * 
       */
      transformData() {
        this.proxyMethod(this.handler, "transformData", [this.proxy.data, this.data, this])
      },
      /**
       * 
       */
      proxyMethod(context, eventName, param) {
        if (!context) {
          return
        }
        let func = context[eventName];
        if (func && typeof func == "function") {
          return func.apply(this, param);
        }
      },
      /**
       * binder
       */
      commitBinder(binderKey, value) {
        this.commit(this.binder[binderKey][1], value)
      },
      /**
       * store
       */
      commit(stateProp, value) {
        this.$store.commit(Config.MUTATIONS.SCREEN.MODIFY, {
          store: stateProp,
          value: value
        });
      },
      /**
       * 
       */
      reload() {
        this.load()
      },
      /**
       * 
       */
      dynamicLoadComponent(columns) {
        let me = this
        if (columns && columns.length) {
          let comps = []
          columns.forEach(column => {
            if (column.type === "component") {
              if (!column.component || !column.component.path || !column.component.name) {
                console.error("Table: component");
                return;
              }
              let configs = []
              if (me.data && me.data.length) {
                for (let i = 0; i < me.data.length; i++) {
                  let item = me.data[i];
                  let config = {}
                  Object.assign(config, column.component.config)
                  config.scope = {
                    value: item[column.prop],
                    prop: column.prop,
                    model: item,
                    index: i
                  }
                  configs.push(config)
                }
              }
              comps.push({
                loader: require("../../" + column.component.path + ".vue"),
                configs: configs
              });
            } else {
              comps.push(null)
            }
          });
          me.components = comps
        }
      },
      /* =================== event =================== */
      onCellClick: function (row, column, cell, event) {
        this.proxyMethod(this.event, "onCellClick", [row, column, cell, event, this])
      },
      onRowClick: function (row, event, column) {
        this.proxyMethod(this.event, "onRowClick", [row, event, column, this])
      },
      onHeaderClick: function (column, event) {
        this.proxyMethod(this.event, "onHeaderClick", [column, event, this])
      },
    },
    watch: {
      option: {
        deep: true,
        handler: function (val) {
          this.load()
        }
      },
      event: {
        deep: true,
        handler: function (val) {

        }
      },
      proxy: {
        deep: true,
        handler: function (val) {
          this.load()
        }
      }
    }
  }
</script>



