<template>
  <div class="config-screen">
    <div class="title"></div>
    <el-row>
      <el-col :span="8" class="tag"></el-col>
      <el-col :span="8">
        <el-input-number v-model="config.base.size.width"
                         size="mini"
                         controls-position="right"
                         :min="0">
        </el-input-number>
        <div class="description"></div>
      </el-col>
      <el-col :span="8">
        <el-input-number v-model="config.base.size.height"
                         size="mini"
                         controls-position="right"
                         :min="0">
        </el-input-number>
        <div class="description"></div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="8" class="tag"></el-col>
      <el-col :span="4">
        <el-color-picker v-model="config.base.backgroundColor"
                         size="mini" show-alpha></el-color-picker>
      </el-col>
      <el-col :span="4">
        <el-button type="text" size="mini" @click="resetBgColor"></el-button>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="8" class="tag"></el-col>
      <el-col :span="16">
        <el-input v-model="config.base.backgroundImage"
                  placeholder=""
                  size="mini" clearable></el-input>
        <img :src="config.base.backgroundImage" class="img-preview">
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="8" class="tag"></el-col>
      <el-col :span="16">
        <el-row>
          <el-radio v-model="config.base.scaleMode" label="0"></el-radio>
        </el-row>
        <el-row>
          <el-radio v-model="config.base.scaleMode" label="1"></el-radio>
        </el-row>
        <el-row>
          <el-radio v-model="config.base.scaleMode" label="2"></el-radio>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    name: "Screen",
    props: ["config", "state"],
    data() {
      return {
        default: {
          // 
          bgColor: null
        }
      }
    },
    created() {
      this.default = {
        bgColor: this.config.base.backgroundColor
      }
    },
    methods: {
      resetBgColor() {
        this.config.base.backgroundColor = this.default.bgColor;
      }
    }
  }
</script>

<style scoped>

</style>



